"use client"; // Keep this to indicate client-side rendering

import { useState } from "react";
import React from "react";
import "./signup.css";
import Link from "next/link";
import { Input } from "@/components/ui/input";

const signup = () => {
  const [inputMessage, setInputMessage] = useState({
    email: "",
    name: "",
    password: "",
    address: "",
  });
  const [isDropdownVisible, setIsDropdownVisible] = useState(false);
  const [isDisabled, setDisabled] = useState(true);

  const handleSignup = (e: React.FormEvent) => {
    e.preventDefault();
    validSignup();
  };

  const validSignup = () => {
    if (
      inputMessage.email.length < 5 ||
      inputMessage.name.length < 5 ||
      inputMessage.password.length < 5 ||
      inputMessage.address.length < 5
    ) {
      setDisabled(true);
    } else {
      setDisabled(false);
    }
  };

  return (
    <div className="page">
    
      <header className="sticky">
         
            <span className="text-2xl font-bold bg-gradient-to-r from-purple-600 to-pink-600 bg-clip-text text-transparent">
              Resonance
            </span>
          
        
        </header>
    
      <h1>Sign Up for an Account</h1>
      <form onSubmit={handleSignup} className="signup">
       
          <input
            type="email"
            value={inputMessage.email}
            required
            placeholder="Enter your email"
            onChange={(e) => {
              validSignup();
              setInputMessage((prev) => ({ ...prev, email: e.target.value }));
            }}
          />

          <input
            type="text"
            value={inputMessage.name}
            required
            placeholder="Enter your name"
            onChange={(e) => {
              validSignup();
              setInputMessage((prev) => ({ ...prev, name: e.target.value }));
            }}
          />

          <input
            type="password"
            value={inputMessage.password}
            required
            placeholder="Enter your password"
            onChange={(e) => {
              validSignup();
              setInputMessage((prev) => ({ ...prev, password: e.target.value }));
            }}
          />

          <input
            type="text"
            value={inputMessage.address}
            required
            placeholder="Enter your address"
            onChange={(e) => {
              setIsDropdownVisible(e.target.value.length > 0);
              validSignup();
              setInputMessage((prev) => ({ ...prev, address: e.target.value }));
            }}
            onBlur={() => setTimeout(() => setIsDropdownVisible(false), 100)}
            onFocus={() =>
              inputMessage.address.length > 0 && setIsDropdownVisible(true)
            }
          />
          {isDropdownVisible && (
            <ul className="dropdown">
              <li>Suggestion</li>
            </ul>

          )}

            <Link href="/">
                <input type="submit" disabled={isDisabled} value="Sign Up" /></Link> 
               
        

                
      </form>
      <button className="login-btn"><a href="login">here</a></button>
     
    </div>
  );
};

export default signup;
